import React, { Component } from "react"
import contact from "./contactform.module.scss"
import axios from "axios"
import { navigate } from "gatsby"

class ContactForm extends Component {
  constructor(props) {
    super(props)
    this.state = {
      sendText: "Send",
      sending: false,
      sendFailed: false,
      allFilled: null,
      firstName: "",
      lastName: "",
      phoneNumber: "",
      email: "",
      message: "",
      token: "",
    }
  }

  componentDidMount() {
    window.grecaptcha
      .execute("6LdoirMUAAAAANySCfCzeGmu5Q5vLvZz_e9YSaZ6", {
        action: "contact",
      })
      .then(token => this.setState({ token }))
  }

  isAllFilled = () =>
    this.state.firstName &&
    this.state.lastName &&
    this.state.phoneNumber &&
    this.state.email &&
    this.state.message
  handleChange = event =>
    this.setState({
      [event.target.name]: event.target.value,
      allFilled: null,
      sendFailed: false,
    })
  handleSubmit = event => {
    event.preventDefault()
    if (this.isAllFilled()) {
      this.setState({ sending: true, allFilled: null })
      axios
        .post(
          "https://a094mzl4c0.execute-api.eu-central-1.amazonaws.com/Prod/process",
          {
            subject: `${this.state.firstName} ${this.state.lastName}`,
            firstName: this.state.firstName,
            lastName: this.state.lastName,
            email: this.state.email,
            number: this.state.phoneNumber,
            message: this.state.message,
            token: this.state.token,
            key: "uohlegalaidcliniccom",
          }
        )
        .then(() =>
          navigate("thanks", { state: { name: this.state.firstName } })
        )
        .catch(() => this.setState({ sendFailed: true, sending: false }))
    } else {
      this.setState({ allFilled: false })
    }
  }

  render() {
    const { sendText, sending, allFilled, sendFailed } = this.state
    return (
      <form onSubmit={this.handleSubmit}>
        <div className={contact.row}>
          <div className={contact.info}>
            <h4>Contact Information</h4>
            <br />
            <label className={contact.sub}>Email</label>:{" "}
            <span>legalaidclinic2002@gmail.com</span>
            <br />
            <label className={contact.sub}>Phone</label>:{" "}
            <span>+252 634 416 493</span>
          </div>

          <div className={contact.form}>
            <div className="form-group">
              <label htmlFor="firstName">First Name</label>
              <input
                type="text"
                className="form-control"
                id="firstName"
                placeholder="First name"
                name="firstName"
                value={this.state.firstName}
                onChange={this.handleChange}
              />
            </div>
            <div className="form-group">
              <label htmlFor="lastName">Last Name</label>
              <input
                type="text"
                className="form-control"
                id="lastName"
                placeholder="Last name"
                name="lastName"
                value={this.state.lastName}
                onChange={this.handleChange}
              />
            </div>
            <div className="form-group">
              <label htmlFor="number">Phone Number</label>
              <input
                type="text"
                className="form-control"
                id="number"
                placeholder="Phone number"
                name="phoneNumber"
                value={this.state.phoneNumber}
                onChange={this.handleChange}
              />
            </div>
            <div className="form-group">
              <label htmlFor="email">Email</label>
              <input
                type="email"
                className="form-control"
                id="email"
                placeholder="Email address"
                name="email"
                value={this.state.email}
                onChange={this.handleChange}
              />
            </div>
            <div className="form-group">
              <label htmlFor="message">Message</label>
              <textarea
                type="text"
                className="form-control"
                rows="5"
                id="message"
                placeholder="Message for Legal Aid Clinic"
                name="message"
                value={this.state.message}
                onChange={this.handleChange}
              />
            </div>
            <button type="submit" className={contact.btnSend}>
              {sending ? (
                <span
                  className="spinner-border spinner-border-md"
                  role="status"
                  aria-hidden="true"
                >
                  {" "}
                </span>
              ) : (
                sendText
              )}
            </button>
            {allFilled === false && (
              <p className="text-danger">Please fill all fields!</p>
            )}
            {sendFailed && (
              <p className="text-danger">
                Oops... failed to send the message. Please try again later!
              </p>
            )}
          </div>
        </div>
      </form>
    )
  }
}

export default ContactForm
